import ReactGA from 'react-ga';

function time12to24(time) {
    time = time.toLowerCase()
    var hours = Number(time.match(/^(\d+)/)[1]);
    const ampm_match = time.match(/(am|pm)$/);
    const ampm = ampm_match ? ampm_match[1] : 'pm';
    const min_match = time.match(/:(\d+)/);
    var minutes = min_match ? Number(min_match[1]) : 0;
    if (!time.includes(':')) {
        minutes = 0;
    }
    if (ampm.toLowerCase() === "pm" && hours < 12) hours = hours + 12;
    if (ampm.toLowerCase() === "am" && hours === 12) hours = hours - 12;
    var sHours = hours.toString();
    var sMinutes = minutes.toString();
    if (hours < 10) sHours = "0" + sHours;
    if (minutes < 10) sMinutes = "0" + sMinutes;
    return sHours + ":" + sMinutes;
}

function time24toSeconds(time) {
    var hours = Number(time.match(/^(\d+)/)[1]);
    const minutes = Number(time.match(/:(\d+)/)[1]);
    return hours * 3600 + minutes * 60;
}

export const getHappyNow = (place) => {
    ReactGA.event({
        category: 'User',
        action: 'Filtered by getHappyNow'
    });
    const date = new Date();
    const day_map = ['sunday', 'monday', 'tuesday', 'wednesday', 'thurday', 'friday', 'saturday', 'sunday'];
    const today = day_map[date.getDay()];
    const hour = date.getHours();
    const minutes = date.getMinutes();
    const todays_times = place.times.filter((time) => time.day === today);
    if (!todays_times || todays_times.length === 0) {
        return false;
    }
    const start_time = time24toSeconds(time12to24(todays_times[0].start_time));
    const end_time = time24toSeconds(time12to24(todays_times[0].end_time));
    const now_time = time24toSeconds(`${hour}:${minutes}`);
    return now_time > start_time && now_time < end_time;
}

export const isNumeric = (str) => {
    if (typeof str == 'number') return true;
    if (typeof str != "string") return false; // we only process strings!  
    return !isNaN(str) && // use type coercion to parse the _entirety_ of the string (`parseFloat` alone does not do this)...
        !isNaN(parseFloat(str)); // ...and ensure strings of whitespace fail
}