import React, { useState, useEffect, createRef } from 'react';
import { CircularProgress, Grid, Typography, FormControl } from '@material-ui/core';
import { FormGroup, FormControlLabel, Checkbox } from '@material-ui/core';

import PlaceDetails from '../PlaceDetails/PlaceDetails';
import useStyles from './styles.js';

const List = ({ places, dogFriendly, setDogFriendly, wifi, setWifi, happyNow, setHappyNow, childClicked, isLoading, coords }) => {
  const [elRefs, setElRefs] = useState([]);
  const classes = useStyles();

  useEffect(() => {
    setElRefs((refs) => Array(places.length).fill().map((_, i) => refs[i] || createRef()));
  }, [places]);

  return (
    <div className={classes.container}>
      {isLoading ? (
        <>
          <Typography variant="h4">Loading Happiness 😀</Typography>
          <div className={classes.loading}>
            <CircularProgress size="5rem" />
          </div>
        </>
      ) : (
        <>
          <Typography variant="h4">Found {places.length} deals nearby</Typography>
          <FormControl className={classes.formControl}>
            <FormGroup row>
              <FormControlLabel
                control={
                  <Checkbox
                    checked={dogFriendly}
                    onChange={(event) => setDogFriendly(event.target.checked)}
                    name="dogFriendly"
                    color="primary"
                  />
                }
                label="Dog Friendly"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={wifi}
                    onChange={(event) => setWifi(event.target.checked)}
                    name="wifi"
                    color="primary"
                  />
                }
                label="Public Wifi"
              />
              <FormControlLabel
                control={
                  <Checkbox
                    checked={happyNow}
                    onChange={(event) => setHappyNow(event.target.checked)}
                    name="happyNow"
                    color="primary"
                  />
                }
                label="Happyning Now"
              />
            </FormGroup>
          </FormControl>
          <Grid container spacing={3} className={classes.list}>
            {places?.map((place, i) => (
              <Grid ref={elRefs[i]} key={i} item xs={12}>
                <PlaceDetails selected={Number(childClicked) === i} refProp={elRefs[i]} place={place} coords={coords} />
              </Grid>
            ))}
          </Grid>
        </>
      )
      }
    </div >
  );
};

export default List;
